import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";
import React, { FC } from "react";
import classes from "./FlowDialog.module.css";

interface FlowDialogProps {
  appearance?: "alert" | "warning";
  header?: React.ReactElement | string;
  message?: React.ReactElement | string;
  show: boolean;
  onClose: () => void;
  onConfirm?: () => void;
}

const FlowDialog: FC<FlowDialogProps> = ({
  show,
  appearance,
  header,
  message,
  onClose,
  onConfirm,
}) => {
  return (
    <Dialog
      maxWidth="xs"
      open={show}
      onClose={onClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">{header || "No Header"}</DialogTitle>
      <DialogContent>
        <DialogContentText
          classes={{ root: classes.DialogContentTextRoot }}
          id="alert-dialog-description"
        >
          {message || ""}
        </DialogContentText>
      </DialogContent>
      <DialogActions className={classes.DialogActions}>
        {appearance !== "alert" && (
          <Button
            onClick={() => {
              onClose();
            }}
            className={classes.Reset}
          >
            cancel
          </Button>
        )}
        <Button
          onClick={() => {
            onConfirm && onConfirm();
            onClose();
          }}
          className={classes.Save}
        >
          Continue
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default FlowDialog;
