import { useMemo, useState } from "react";
import { ChatFlowHistoryInterface } from "Shared/Interfaces/ChatFlowHistoryInterface";

export const useHistory = () => {
  const [history, setHistory] = useState<ChatFlowHistoryInterface[]>([]);
  const [historyIndex, setHistoryIndex] = useState<number>(-1);

  const saveHistory = (state: ChatFlowHistoryInterface) => {
    setHistory([...history.slice(0, historyIndex + 1), state]);
    setHistoryIndex(historyIndex + 1);
  };

  const resetHistory = () => {
    setHistory([]);
    setHistoryIndex(-1);
  };

  const canUndo = useMemo(
    () => history.length > 0 && historyIndex > 0,
    [history, historyIndex]
  );

  const canRedo = useMemo(
    () => history.length > 0 && historyIndex < history.length - 1,
    [history, historyIndex]
  );

  const undo = () => {
    if (!canUndo) return;
    if (historyIndex === 0) return;
    setHistoryIndex(historyIndex - 1);
  };

  const redo = () => {
    if (!canRedo) return;
    if (historyIndex === history.length - 1) return;
    setHistoryIndex(historyIndex + 1);
  };

  return {
    history,
    setHistory,
    historyIndex,
    setHistoryIndex,
    resetHistory,
    saveHistory,
    canUndo,
    undo,
    canRedo,
    redo,
  };
};
