import { Node } from "react-flow-renderer";
import { HandleType } from "Shared/Enums/ChatFlowNodeEnum";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import { UniqueEntityID } from "Utils/UniqueEntityID";

export class NodeCopy {
  private static commonNewNode = (
    node: Node,
    newId: string,
    copySourceHandles: boolean = true
  ): Node => ({
    ...node,
    id: newId,
    data: {
      ...node.data,
      nodeDataLabel: undefined,
      handles: copySourceHandles
        ? node.data.handles.map((handle: any) => ({
            ...handle,
            id: new UniqueEntityID().toString(),
          }))
        : node.data.handles.filter(
            (handle: any) => handle.type === HandleType.TARGET
          ),
    },
  });

  private static newSourceHandles = (node: Node) =>
    node.data.handles.filter(
      (handle: any) => handle.type === HandleType.SOURCE
    );

  public static CommonCopy = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => ({
    newNode: this.commonNewNode(node, newId),
    newChatMsg: {
      ...chatMsg,
      id: newId,
      trigger_id: undefined,
      msg_settings: {
        ...chatMsg.msg_settings,
        is_first_msg: false,
        is_last_msg: false,
      },
    },
  });

  public static Message100 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        trigger_id: undefined,
        options: chatMsg.options?.map((option, i) => ({
          ...option,
          id: sourceHandles[i].id,
        })),
      },
    };
  };

  public static Message101 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        options: chatMsg.options?.map((option, i) => ({
          ...option,
          id: sourceHandles[i].id,
          trigger_id: undefined,
        })),
      },
    };
  };

  public static Message108 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        ...(chatMsg.condition
          ? {
              condition: {
                ...chatMsg.condition,
                conditions: chatMsg.condition.conditions?.map(
                  (condition, i) => ({
                    ...condition,
                    id: sourceHandles[i].id,
                    trigger_id: undefined,
                  })
                ),
              },
            }
          : {}),
      },
    };
  };

  public static Message109 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        ...(chatMsg.condition
          ? {
              condition: {
                ...chatMsg.condition,
                conditions: chatMsg.condition.conditions.map(
                  (condition, i) => ({
                    ...condition,
                    id: sourceHandles[i].id,
                    trigger_id: undefined,
                  })
                ),
              },
            }
          : {}),
      },
    };
  };

  public static Message110 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        ...(chatMsg.action
          ? {
              action: {
                buttons: chatMsg.action.buttons?.map((button, i) => ({
                  ...button,
                  button_id: sourceHandles[i].id,
                  trigger_id: undefined,
                })),
              },
            }
          : {}),
      },
    };
  };

  public static Message111 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId, false);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        action: {
          ...chatMsg.action,
          sections: [],
        },
      },
    };
  };

  public static Message112 = (
    chatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    node: Node,
    newId: string
  ): {
    newNode: Node;
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface;
  } => {
    const newNode = this.commonNewNode(node, newId);
    const sourceHandles = this.newSourceHandles(newNode);
    return {
      newNode,
      newChatMsg: {
        ...chatMsg,
        id: newId,
        ...(chatMsg.condition
          ? {
              condition: {
                ...chatMsg.condition,
                conditions: chatMsg.condition.conditions.map(
                  (condition, i) => ({
                    ...condition,
                    id: sourceHandles[i].id,
                    trigger_id: undefined,
                  })
                ),
              },
            }
          : {}),
      },
    };
  };
}
