import { ChatFlowSettingInterface } from "Shared/Interfaces/ChatFlowSettingInterface";
import { AuthenticatedReq } from "Utils/Axios";

export default class ChatFlowApi {
  public static getAllDataFields = async () => {
    return await AuthenticatedReq(`/lead-data-fields`, "GET");
  };

  public static createLeadDataField = async (data: any) => {
    return await AuthenticatedReq(`/lead-data-fields/`, "POST", { data });
  };

  public static getChatFlowData = async (chatflow_id: string) => {
    return await AuthenticatedReq(`/chatflow/${chatflow_id}`, "GET");
  };

  public static putChatFlowSettings = async (
    chatflow_id: string,
    data: ChatFlowSettingInterface
  ) => {
    return await AuthenticatedReq(`/chatbots/${chatflow_id}`, "PUT", { data });
  };

  public static putChatFlowData = async (chatflow_id: string, data: any) => {
    return await AuthenticatedReq(`/chatflow/save-data/${chatflow_id}`, "PUT", {
      data,
    });
  };
}
