import { createContext, FC, useContext, useEffect, useState } from "react";
import { Node, useEdgesState } from "react-flow-renderer";
import { useParams } from "react-router-dom";

import { ChatFlowSettingInterface } from "Shared/Interfaces/ChatFlowSettingInterface";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import { ChatFlowNode } from "Shared/Interfaces/ChatFlowNodeInterface";
import { FlowDataContextState } from "./types";
import { useSnacks } from "Contexts/Snack";
import { useFlowHistory } from "Contexts/ChatFlowDashboard";
import { IDataFields } from "Shared/Interfaces/LeadDataFieldsInterface";
import ChatFlowApi from "Apis/ChatFlowApi";

export const FlowDataContext = createContext<FlowDataContextState | undefined>(
  undefined
);

export const useFlowData = () => {
  const context = useContext(FlowDataContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of FlowDataProvider, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const FlowDataProvider: FC<any> = ({ children }) => {
  const { chatflow_id } = useParams();
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { resetHistory, saveHistory } = useFlowHistory();
  // const [chatbot_id, setChatbotId] = useState(1);
  const [reloadDrawer, setReloadDrawer] = useState(0);
  const [refreshNode, setRefreshNode] = useState(0);
  const [dataFields, setDataFields] = useState<IDataFields[]>([]);
  const [editingData, toggleEditingData] = useState<boolean>(false);
  const [nodes, setNodes] = useState<Node<any>[]>([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [chatMsgs, setChatMsgs] = useState<ChatFlowMsg.ChatFlowMsgInterface[]>(
    []
  );
  const [copiedNode, setCopiedNode] = useState<
    ChatFlowNode.ChatFlowNodeInterface | undefined
  >();
  const [copiedChatMsgs, setCopiedChatMsgs] = useState<
    ChatFlowMsg.ChatFlowMsgInterface | undefined
  >();
  const [flowSettingsLoaded, setFlowSettingLoaded] = useState<boolean>(false);
  const [flowSettings, setFlowSettings] = useState<ChatFlowSettingInterface>({
    auto_reminder: {
      response: { default_message: "", languages: [] },
      status: false,
      trigger_time: 30,
      send_last_message: false,
    },
    disable_reply: false,
    reset_session_in: 12,
  });
  const [currentMsgIndex, setCurrentMsgIndex] = useState<number | undefined>(
    undefined
  );
  const [currentNodeIndex, setCurrentNodeIndex] = useState<number | undefined>(
    undefined
  );

  const addChatMsg = (
    msg: ChatFlowMsg.ChatFlowMsgInterface,
    newNodes: Node<any>[]
  ) => {
    const newChatMsgs: ChatFlowMsg.ChatFlowMsgInterface[] =
      chatMsgs.concat(msg);
    setChatMsgs(newChatMsgs);
    setCurrentMsgIndex(chatMsgs.length);
    saveHistory({ nodes: newNodes, edges, chatMsgs: newChatMsgs });
  };

  const clearSelectedMsg = () => {
    setCurrentMsgIndex(undefined);
  };

  const onClickChatMsg = (id: string) => {
    const current = chatMsgs.findIndex((msg) => msg.id === id);
    if (current === -1) return;
    setCurrentMsgIndex(current);
  };

  const OnChatMsgUpdate = (
    current_msg: ChatFlowMsg.ChatFlowMsgInterface,
    index?: number
  ) => {
    const isIndex = typeof index === "number";
    const currentIndex = typeof currentMsgIndex === "number";

    if (isIndex) {
      // setCurrentChatMsg(current_msg);
      const newChatMsgs: ChatFlowMsg.ChatFlowMsgInterface[] = chatMsgs;
      newChatMsgs[index] = current_msg;
      setChatMsgs(newChatMsgs);
      // saveHistory({ nodes, edges, chatMsgs: newChatMsgs });
    } else if (currentIndex) {
      const newChatMsgs: ChatFlowMsg.ChatFlowMsgInterface[] = chatMsgs;
      newChatMsgs[currentMsgIndex] = current_msg;
      setChatMsgs(newChatMsgs);
      // saveHistory({ nodes, edges, chatMsgs: newChatMsgs });
    }
  };

  const onChatMsgDelete = (index: number, newNodes: Node<any>[]) => {
    const newChatMsgs: ChatFlowMsg.ChatFlowMsgInterface[] = chatMsgs;
    newChatMsgs.splice(index, 1);
    setChatMsgs(newChatMsgs);
    saveHistory({ nodes: newNodes, edges, chatMsgs: newChatMsgs });
    clearSelectedMsg();
  };

  const saveChatFlowData = async () => {
    try {
      if (chatflow_id) {
        const res = await ChatFlowApi.putChatFlowData(chatflow_id, {
          edges,
          nodes,
          msgs: chatMsgs,
        });

        if (!res.success) {
          throw Error(res.message);
        }
        setSuccessMessage(res.message);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };

  const fetchChatFlowData = async () => {
    try {
      if (chatflow_id) {
        const res = await ChatFlowApi.getChatFlowData(chatflow_id);
        if (!res.success) {
          throw Error(res.message);
        }
        resetHistory();
        nodes.length < 1 && setNodes(res.payload.nodes);
        edges.length < 1 && setEdges(res.payload.edges);
        chatMsgs.length < 1 && setChatMsgs(res.payload.msgs);
        res.payload.setting && setFlowSettings(res.payload.setting);
        setFlowSettingLoaded(true);
        saveHistory({
          nodes: res.payload.nodes,
          edges: res.payload.edges,
          chatMsgs: res.payload.msgs,
        });
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };

  const fetchAllDataFields = async () => {
    try {
      const res = await ChatFlowApi.getAllDataFields();
      if (!res.success) {
        throw Error(res.message);
      }
      setDataFields(res.payload);
    } catch (error: any) {
      console.error(error.message);
    }
  };

  useEffect(() => {
    saveHistory({ nodes, edges, chatMsgs });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <FlowDataContext.Provider
      value={{
        chatMsgs,
        addChatMsg,
        clearSelectedMsg,
        onClickChatMsg,
        OnChatMsgUpdate,
        currentMsgIndex,
        currentChatMsg:
          typeof currentMsgIndex === "number"
            ? chatMsgs[currentMsgIndex]
            : undefined,
        flowSettings,
        setFlowSettings,
        flowSettingsLoaded,
        reloadDrawer,
        setReloadDrawer,
        refreshNode,
        setRefreshNode,
        onChatMsgDelete,
        setChatMsgs,
        currentNode:
          typeof currentNodeIndex === "number"
            ? (nodes[currentNodeIndex] as ChatFlowNode.ChatFlowNodeInterface)
            : undefined,
        currentNodeIndex,
        edges,
        nodes,
        dataFields,
        onEdgesChange,
        setCurrentNodeIndex,
        setEdges,
        setNodes,
        setDataFields,
        saveChatFlowData,
        fetchChatFlowData,
        fetchAllDataFields,
        copiedNode,
        copiedChatMsgs,
        setCopiedNode,
        setCopiedChatMsgs,
        editingData,
        toggleEditingData,
      }}
    >
      {children}
    </FlowDataContext.Provider>
  );
};

export default FlowDataProvider;
