import { useHistory } from "Hooks/ChatFlowDashboard/useHistory";
import React, { FC, useContext } from "react";
import { FlowHistoryContextType } from "./types";

export const FlowHistoryContext = React.createContext<
  FlowHistoryContextType | undefined
>(undefined);

export const useFlowHistory = () => {
  let context = useContext(FlowHistoryContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const FlowHistoryProvider: FC<any> = ({ children }) => {
  const {
    history,
    setHistory,
    historyIndex,
    setHistoryIndex,
    resetHistory,
    saveHistory,
    canUndo,
    undo,
    canRedo,
    redo,
  } = useHistory();

  return (
    <FlowHistoryContext.Provider
      value={{
        history,
        setHistory,
        historyIndex,
        setHistoryIndex,
        resetHistory,
        saveHistory,
        canUndo,
        undo,
        canRedo,
        redo,
      }}
    >
      {children}
    </FlowHistoryContext.Provider>
  );
};

export default FlowHistoryProvider;
