import { createContext, FC, memo, useContext, useEffect } from "react";
import { getRandomColor } from "Utils";
import { useFlowData } from "./FlowDataProvider";
import {
  AddOptionProps,
  FlowServicesContextState,
  RemoveOptionProps,
} from "./types";
import { HandlePosition, HandleType } from "Shared/Enums/ChatFlowNodeEnum";
import { ChatFlowNode } from "Shared/Interfaces/ChatFlowNodeInterface";
import { UniqueEntityID } from "Utils/UniqueEntityID";
import { OptionType } from "Shared/Enums/OptionType";
import { NodeCopy } from "Services/ChatFlowService/NodeCopyService";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import { Node } from "react-flow-renderer";
import { MessageType } from "Shared/Enums/ChatFlowMsgEnum";
import { useFlowHistory } from "Contexts/ChatFlowDashboard";

export const FlowServicesContext = createContext<
  FlowServicesContextState | undefined
>(undefined);

export const useFlowServices = () => {
  let context = useContext(FlowServicesContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const FlowServicesProvider: FC<any> = ({ children }) => {
  const { history, historyIndex, saveHistory } = useFlowHistory();
  const {
    currentMsgIndex,
    chatMsgs,
    setReloadDrawer,
    setRefreshNode,
    currentNodeIndex,
    currentNode,
    currentChatMsg,
    edges,
    nodes,
    setEdges,
    setNodes,
    setChatMsgs,
    copiedChatMsgs,
    copiedNode,
    setCopiedNode,
    setCopiedChatMsgs,
  } = useFlowData();

  const addOption = ({ type, index, makeHandle = false }: AddOptionProps) => {
    const currentChatMsg =
      typeof currentMsgIndex === "number" && chatMsgs[currentMsgIndex];
    const currentNode =
      typeof currentNodeIndex === "number"
        ? (nodes[currentNodeIndex] as ChatFlowNode.ChatFlowNodeInterface)
        : undefined;

    if (!currentNode || !currentChatMsg) return;

    const random_color = getRandomColor();
    const option_id = new UniqueEntityID().toString(); //Date.now().toString();
    // const new_id = new UniqueEntityID().toString();
    // const current_id = currentNode.id;

    if (makeHandle) {
      if (type === OptionType.CONDITION) {
        currentNode.data.handles?.unshift({
          type: HandleType.SOURCE,
          id: option_id,
          position: HandlePosition.BOTTOM,
          style: { border: `1px solid ${random_color}` },
        });
      } else {
        currentNode.data.handles?.push({
          type: HandleType.SOURCE,
          id: option_id,
          position: HandlePosition.BOTTOM,
          style: { border: `1px solid ${random_color}` },
        });
      }
    }

    switch (type) {
      case OptionType.OPTION:
        currentChatMsg.options?.push({
          label: "",
          id: option_id,
          value: "",
          style: { border: `2px solid ${random_color}` },
        });
        break;
      case OptionType.BUTTON:
        currentChatMsg.action &&
          currentChatMsg.action.buttons?.push({
            button_id: option_id,
            title: "",
            type: "reply",
            style: { border: `2px solid ${random_color}` },
          });
        break;
      case OptionType.LIST_SECTION:
        currentChatMsg.action &&
          currentChatMsg.action.sections?.push({
            id: option_id,
            title: "",
            rows: [],
          });
        break;
      case OptionType.LIST_SECTION_ITEM:
        currentChatMsg.action &&
          index !== undefined &&
          currentChatMsg.action.sections &&
          currentChatMsg.action.sections[index].rows.push({
            id: option_id,
            title: "",
            description: "",
            style: { borderLeft: `4px solid ${random_color}` },
          });
        break;
      case OptionType.CONDITION:
        currentChatMsg.condition &&
          currentChatMsg.condition.conditions &&
          currentChatMsg.condition.conditions.unshift({
            id: option_id,
            values: "",
            removable: true,
            style: { border: `2px solid ${random_color}` },
          });
        break;
      default:
        break;
    }

    setReloadDrawer((prev) => (prev += 1));
    makeHandle && setRefreshNode((prev) => (prev += 1));
  };

  const removeOption = ({
    type,
    removeHandle = false,
    id,
    index,
  }: RemoveOptionProps) => {
    const currentChatMsg =
      typeof currentMsgIndex === "number" && chatMsgs[currentMsgIndex];
    const currentNode =
      typeof currentNodeIndex === "number"
        ? (nodes[currentNodeIndex] as ChatFlowNode.ChatFlowNodeInterface)
        : undefined;

    if (!currentNode || !currentChatMsg) return;

    if (removeHandle) {
      const optionIndex = currentNode.data.handles.findIndex(
        (e) => e.id === id
      );
      optionIndex !== -1 && currentNode.data.handles.splice(optionIndex, 1);
    }

    if (type === OptionType.LIST_SECTION) {
      currentChatMsg.action &&
        index !== undefined &&
        currentChatMsg.action.sections &&
        currentChatMsg.action.sections[index].rows?.forEach((row) => {
          const optionIndex = currentNode.data.handles.findIndex(
            (e) => e.id === row.id
          );
          optionIndex !== -1 && currentNode.data.handles.splice(optionIndex, 1);
        });
    }

    switch (type) {
      case OptionType.OPTION:
        const optionIndex = currentChatMsg.options?.findIndex(
          (e) => e.id === id
        );

        typeof optionIndex === "number" &&
          currentChatMsg.options?.splice(optionIndex, 1);
        break;
      case OptionType.BUTTON:
        const buttonIndex =
          currentChatMsg.action &&
          currentChatMsg.action.buttons?.findIndex((e) => e.button_id === id);

        typeof buttonIndex === "number" &&
          currentChatMsg.action &&
          currentChatMsg.action.buttons?.splice(buttonIndex, 1);
        break;
      case OptionType.LIST_SECTION:
        const sectionIndex =
          currentChatMsg.action &&
          currentChatMsg.action.sections?.findIndex((e) => e.id === id);

        typeof sectionIndex === "number" &&
          currentChatMsg.action &&
          currentChatMsg.action.sections?.splice(sectionIndex, 1);
        break;
      case OptionType.LIST_SECTION_ITEM:
        const rowIndex =
          currentChatMsg.action &&
          index !== undefined &&
          currentChatMsg.action.sections &&
          currentChatMsg.action.sections[index].rows.findIndex(
            (e) => e.id === id
          );

        typeof rowIndex === "number" &&
          currentChatMsg.action &&
          index !== undefined &&
          currentChatMsg.action.sections &&
          currentChatMsg.action.sections[index].rows?.splice(rowIndex, 1);
        break;
      case OptionType.CONDITION:
        const conditionIndex =
          currentChatMsg.condition &&
          currentChatMsg.condition.conditions?.findIndex((e) => e.id === id);

        typeof conditionIndex === "number" &&
          currentChatMsg.condition &&
          currentChatMsg.condition.conditions?.splice(conditionIndex, 1);
        break;
      default:
        break;
    }
    setReloadDrawer((prev) => (prev += 1));
    removeHandle && setRefreshNode((prev) => (prev += 1));
  };

  const addCopiedNode = (
    newNode: Node,
    newChatMsg: ChatFlowMsg.ChatFlowMsgInterface
  ) => {
    if (!newNode || !newChatMsg) {
      return;
    }
    const newChatMsgs: ChatFlowMsg.ChatFlowMsgInterface[] =
      chatMsgs.concat(newChatMsg);
    setChatMsgs(newChatMsgs);
    const newNodes: Node<any>[] = nodes.concat({
      ...newNode,
      position: {
        x: newNode.position.x + 50,
        y: newNode.position.y + 50,
      },
    });
    setNodes(newNodes);
    saveHistory({ nodes: newNodes, edges, chatMsgs: newChatMsgs });
  };

  const getCopiedNode = (
    messageType: MessageType,
    newId: string,
    currentChatMsg: ChatFlowMsg.ChatFlowMsgInterface,
    currentNode: any
  ): { newNode: Node; newChatMsg: ChatFlowMsg.ChatFlowMsgInterface } => {
    switch (messageType) {
      case MessageType.TYPE100:
        return NodeCopy.Message100(currentChatMsg, currentNode, newId);
      case MessageType.TYPE101:
        return NodeCopy.Message101(currentChatMsg, currentNode, newId);
      case MessageType.TYPE102:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
      case MessageType.TYPE103:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
      case MessageType.TYPE104:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
      case MessageType.TYPE105:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
      case MessageType.TYPE107:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
      case MessageType.TYPE108:
        return NodeCopy.Message108(currentChatMsg, currentNode, newId);
      case MessageType.TYPE109:
        return NodeCopy.Message109(currentChatMsg, currentNode, newId);
      case MessageType.TYPE110:
        return NodeCopy.Message110(currentChatMsg, currentNode, newId);
      case MessageType.TYPE111:
        return NodeCopy.Message111(currentChatMsg, currentNode, newId);
      case MessageType.TYPE112:
        return NodeCopy.Message112(currentChatMsg, currentNode, newId);
      case MessageType.TYPE113:
        return NodeCopy.CommonCopy(currentChatMsg, currentNode, newId);
    }
  };

  const copyNode = () => {
    if (currentChatMsg && currentNode) {
      setCopiedNode(currentNode);
      setCopiedChatMsgs(currentChatMsg);
    }
  };

  const pasteNode = () => {
    if (copiedChatMsgs && copiedNode) {
      const messageType: MessageType = copiedChatMsgs.type;
      const newId = new UniqueEntityID().toString();
      const { newNode, newChatMsg } = getCopiedNode(
        messageType,
        newId,
        copiedChatMsgs,
        copiedNode
      );
      if (newNode && newChatMsg) {
        addCopiedNode(newNode, newChatMsg);
      }
    }
  };

  const duplicateNode = () => {
    if (currentChatMsg && currentNode) {
      const messageType: MessageType = currentChatMsg.type;
      const newId = new UniqueEntityID().toString();
      const { newNode, newChatMsg } = getCopiedNode(
        messageType,
        newId,
        currentChatMsg,
        currentNode
      );
      if (newNode && newChatMsg) {
        addCopiedNode(newNode, newChatMsg);
      }
    }
  };

  useEffect(() => {
    if (history[historyIndex]) {
      setNodes(history[historyIndex].nodes);
      setEdges(history[historyIndex].edges);
      setChatMsgs(history[historyIndex].chatMsgs);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [historyIndex]);

  return (
    <FlowServicesContext.Provider
      value={{
        addOption,
        removeOption,
        copyNode,
        pasteNode,
        duplicateNode,
      }}
    >
      {children}
    </FlowServicesContext.Provider>
  );
};

export default memo(FlowServicesProvider);
