import {
  createContext,
  CSSProperties,
  FC,
  useCallback,
  useContext,
} from "react";
import {
  addEdge,
  Edge,
  Node,
  OnEdgesDelete,
  OnEdgeUpdateFunc,
  OnNodesDelete,
  updateEdge,
  useKeyPress,
} from "react-flow-renderer";
import NodeModelService from "Services/ChatFlowService/NodeModelService";
import { FlowEdge } from "Shared/Enums/FlowEdge";
import { UniqueEntityID } from "Utils/UniqueEntityID";
import { CurrentDrawerModes, useFlowDrawer } from "./FlowDrawerProvider";
import { useFlowData } from "./FlowDataProvider";
import { FlowNodeServicesContextState } from "./types";
import { NodeDataLabel } from "Shared/Enums/ChatFlowNodeEnum";
import { useFlowHistory } from "Contexts/ChatFlowDashboard";

export const FlowNodeServicesContext = createContext<
  FlowNodeServicesContextState | undefined
>(undefined);

export const useFlowNodeServices = () => {
  let context = useContext(FlowNodeServicesContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const FlowNodeServicesProvider: FC<any> = ({ children }) => {
  const { saveHistory } = useFlowHistory();
  const { setCurrentDrawerMode } = useFlowDrawer();
  const {
    addChatMsg,
    onClickChatMsg,
    clearSelectedMsg,
    currentChatMsg,
    chatMsgs,
    setReloadDrawer,
    onChatMsgDelete,
    edges,
    setEdges,
    nodes,
    setNodes,
    setCurrentNodeIndex,
    currentNodeIndex,
    setRefreshNode,
    currentNode,
  } = useFlowData();

  const spacePressed = useKeyPress("Space");

  const dragOverFlow = useCallback((event: any) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = "move";
  }, []);

  const onEdgeConnect = (params: Edge) => {
    const type = FlowEdge.SMOOTHSTEP;
    if (params.source === params.target) return;

    const chatMsg = chatMsgs.find((msg) => msg.id === params.source);
    if (!chatMsg) return;

    const style: CSSProperties = { strokeWidth: 3 };

    switch (chatMsg.type) {
      case 101:
        for (const option of chatMsg.options || []) {
          if (option.id !== params.sourceHandle) continue;
          option.trigger_id = params.target;
          const newEdges: Edge<any>[] = addEdge(
            { ...params, type, style },
            edges
          );
          setEdges(newEdges);
          saveHistory({ nodes, edges: newEdges, chatMsgs });
          break;
        }
        break;
      case 108:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== params.sourceHandle) continue;
          condition.trigger_id = params.target;
          const newEdges: Edge<any>[] = addEdge(
            { ...params, type, style },
            edges
          );
          setEdges(newEdges);
          saveHistory({ nodes, edges: newEdges, chatMsgs });
          break;
        }
        break;
      case 109:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== params.sourceHandle) continue;
          condition.trigger_id = params.target;
          const newEdges: Edge<any>[] = addEdge(
            { ...params, type, style },
            edges
          );
          setEdges(newEdges);
          saveHistory({ nodes, edges: newEdges, chatMsgs });
          break;
        }
        break;
      case 110:
        for (const button of chatMsg.action?.buttons || []) {
          if (button.button_id !== params.sourceHandle) continue;
          button.trigger_id = params.target;
          const newEdges: Edge<any>[] = addEdge(
            { ...params, type, style },
            edges
          );
          setEdges(newEdges);
          saveHistory({ nodes, edges: newEdges, chatMsgs });
          break;
        }
        break;
      case 111:
        for (const section of chatMsg.action?.sections || []) {
          for (const row of section.rows) {
            if (row.id !== params.sourceHandle) continue;
            row.trigger_id = params.target;
            const newEdges: Edge<any>[] = addEdge(
              { ...params, type, style },
              edges
            );
            setEdges(newEdges);
            saveHistory({ nodes, edges: newEdges, chatMsgs });
            break;
          }
        }
        break;
      case 112:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== params.sourceHandle) continue;
          condition.trigger_id = params.target;
          const newEdges: Edge<any>[] = addEdge(
            { ...params, type, style },
            edges
          );
          setEdges(newEdges);
          saveHistory({ nodes, edges: newEdges, chatMsgs });
          break;
        }
        break;
      default:
        chatMsg.trigger_id = params.target;
        const newEdges: Edge<any>[] = addEdge(
          { ...params, type, style },
          edges
        );
        setEdges(newEdges);
        saveHistory({ nodes, edges: newEdges, chatMsgs });
        break;
    }
  };

  const onEdgeUpdate: OnEdgeUpdateFunc = (OldEdge, newConnection) => {
    const chatMsg = chatMsgs.find((msg) => msg.id === newConnection.source);
    if (!chatMsg || !newConnection.target) return;
    switch (chatMsg.type) {
      case 101:
        for (const option of chatMsg.options || []) {
          if (option.id !== OldEdge.sourceHandle) continue;
          option.trigger_id = newConnection.target;
          break;
        }
        break;
      case 108:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== OldEdge.sourceHandle) continue;
          condition.trigger_id = newConnection.target;
          break;
        }
        break;
      case 109:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== OldEdge.sourceHandle) continue;
          condition.trigger_id = newConnection.target;
          break;
        }
        break;
      case 110:
        if (chatMsg.action) {
          for (const button of chatMsg.action.buttons || []) {
            if (button.button_id !== OldEdge.sourceHandle) continue;
            button.trigger_id = newConnection.target;
            break;
          }
        }
        break;
      case 111:
        for (const button of chatMsg.action?.sections || []) {
          for (const row of button.rows) {
            if (row.id !== OldEdge.sourceHandle) continue;
            row.trigger_id = newConnection.target;
            break;
          }
        }
        break;
      case 112:
        for (const condition of chatMsg.condition?.conditions || []) {
          if (condition.id !== OldEdge.sourceHandle) continue;
          condition.trigger_id = newConnection.target;
          break;
        }
        break;
      default:
        chatMsg.trigger_id = newConnection.target;
        const newEdges: Edge<any>[] = updateEdge(OldEdge, newConnection, edges);
        setEdges(newEdges);
        saveHistory({ nodes, edges: newEdges, chatMsgs });
        break;
    }
  };

  const onEdgesDelete: OnEdgesDelete = (edges) => {
    for (const edge of edges) {
      const chatMsg = chatMsgs.find((msg) => msg.id === edge.source);
      if (!chatMsg) return;

      switch (chatMsg.type) {
        case 101:
          for (const option of chatMsg.options || []) {
            if (option.id !== edge.sourceHandle) continue;
            option.trigger_id = undefined;
            break;
          }
          break;
        case 108:
          for (const condition of chatMsg.condition?.conditions || []) {
            if (condition.id !== edge.sourceHandle) continue;
            condition.trigger_id = undefined;
            break;
          }
          break;
        case 109:
          for (const condition of chatMsg.condition?.conditions || []) {
            if (condition.id !== edge.sourceHandle) continue;
            condition.trigger_id = undefined;
            break;
          }
          break;
        case 110:
          for (const button of chatMsg.action?.buttons || []) {
            if (button.button_id !== edge.sourceHandle) continue;
            button.trigger_id = undefined;
            break;
          }
          break;
        case 111:
          for (const button of chatMsg.action?.sections || []) {
            for (const row of button.rows) {
              if (row.id !== edge.sourceHandle) continue;
              row.trigger_id = undefined;
              break;
            }
          }
          break;
        case 112:
          for (const condition of chatMsg.condition?.conditions || []) {
            if (condition.id !== edge.sourceHandle) continue;
            condition.trigger_id = undefined;
            break;
          }
          break;
        default:
          chatMsg.trigger_id = undefined;
          break;
      }
      saveHistory({ nodes, edges, chatMsgs });
    }
  };

  const onNodesDelete: OnNodesDelete = (deleted_nodes) => {
    for (const node of deleted_nodes) {
      const chatMsgIndex = chatMsgs.findIndex((msg) => msg.id === node.id);
      const nodeIndex = nodes.findIndex((msg) => msg.id === node.id);
      if (chatMsgIndex === -1 || nodeIndex === -1) continue;
      const newNodes = nodes;
      newNodes.splice(nodeIndex, 1);
      setNodes(newNodes);
      onChatMsgDelete(chatMsgIndex, newNodes);
    }
  };

  const addFlowNode = (type: number, label: string, position: any) => {
    const id = new UniqueEntityID();

    const nodeModelService = new NodeModelService(
      id.toString(),
      type,
      position,
      <p>{label}</p>
    );
    const node_data = nodeModelService.get_node_data();
    const newNodes = nodes.concat([node_data.flow_node as any]);
    setNodes(newNodes);
    setCurrentNodeIndex(nodes.length);
    addChatMsg(node_data.flow_msg, newNodes);
  };

  const onNodeDragEnd = (node: Node) => {
    const draggedElement = nodes.findIndex(
      ({ id }: { id: string }) => id === node.id
    );

    nodes[draggedElement] = {
      ...nodes[draggedElement],
      position: node.position,
    };
    saveHistory({ nodes, edges, chatMsgs });
  };

  // stress test code
  // const addFlowNode = (type: number, label: string, position: any) => {
  //   let nodes: any[] = [];

  //   let xNodes = 10,
  //     yNodes = 10;

  //   for (let y = 0; y < yNodes; y++) {
  //     for (let x = 0; x < xNodes; x++) {
  //       const position = { x: x * 100, y: y * 50 };
  //       const id = new UniqueEntityID();

  //       const nodeModelService = new NodeModelService(
  //         id.toString(),
  //         type,
  //         position,
  //         <p>{label}</p>
  //       );
  //       const node_data = nodeModelService.get_node_data();

  //       nodes.push(node_data.flow_node);
  //       setCurrentNodeIndex(nodes.length);
  //       addChatMsg(node_data.flow_msg, nodes);
  //     }
  //   }

  //   setNodes((prevState) => prevState.concat(nodes));
  // };

  const onNodeClick = (
    _event: React.MouseEvent<Element, MouseEvent>,
    node: Node<any>
  ) => {
    setCurrentDrawerMode(CurrentDrawerModes.NODE);
    if (node.id !== currentChatMsg?.id) {
      const node_index = nodes.findIndex((f_node) => f_node.id === node.id);
      if (node_index === -1) return;

      setCurrentNodeIndex(node_index);
      onClickChatMsg(node.id);
      setReloadDrawer((prev) => (prev += 1));
    }
  };

  const deselectNode = () => {
    setCurrentNodeIndex(undefined);
    setCurrentDrawerMode(CurrentDrawerModes.MENU);
    clearSelectedMsg();
  };

  const updateNodeLabel = (label: NodeDataLabel | undefined) => {
    if (typeof currentNodeIndex !== "number") return;

    // setNodes((prevState) => {
    //   prevState[currentNodeIndex] = {
    //     ...prevState[currentNodeIndex],
    //     data: {
    //       ...prevState[currentNodeIndex].data,
    //       nodeDataLabel: label,
    //     },
    //   };
    //   return prevState;
    if (currentNode) {
      currentNode.data.nodeDataLabel = label;
    }

    setRefreshNode((prev) => (prev += 1));
  };

  return (
    <FlowNodeServicesContext.Provider
      value={{
        dragOverFlow,
        onConnect: onEdgeConnect,
        spacePressed,
        addFlowNode,
        onNodeClick,
        deselectNode,
        updateNodeLabel,
        onEdgeUpdate,
        onEdgesDelete,
        onNodesDelete,
        onNodeDragEnd,
      }}
    >
      {children}
    </FlowNodeServicesContext.Provider>
  );
};

export default FlowNodeServicesProvider;
