import { createContext, FC, useContext, useState } from "react";
import { FlowDrawerContextState } from "./types";

export const FlowDrawerContext = createContext<
  FlowDrawerContextState | undefined
>(undefined);

export const useFlowDrawer = () => {
  let context = useContext(FlowDrawerContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

export enum CurrentDrawerModes {
  MENU = "MENU",
  NODE = "NODE",
  SETTING = "SETTING",
}

const FlowDrawerProvider: FC<any> = ({ children }) => {
  const [isDrawerOpen, setDrawerOpen] = useState(true);

  const [currentDrawerMode, setCurrentDrawerMode] =
    useState<CurrentDrawerModes>(CurrentDrawerModes.MENU);

  return (
    <FlowDrawerContext.Provider
      value={{
        isDrawerOpen,
        setDrawerOpen,
        currentDrawerMode,
        setCurrentDrawerMode,
      }}
    >
      {children}
    </FlowDrawerContext.Provider>
  );
};

export default FlowDrawerProvider;
