import React, { ReactElement, useContext, useState } from "react";
import FlowDialog from "../../Components/ChatFlowDashboard/Dialogs/FlowDialog";
import { FlowDialogContextType } from "./types";

export const FlowDialogContext = React.createContext<
  FlowDialogContextType | undefined
>(undefined);

export const useFlowDialog = () => {
  let context = useContext(FlowDialogContext);

  // If context is undefined, we know we used RadioGroupItem
  // outside of our provider so we can throw a more helpful
  // error!
  if (context === undefined) {
    throw Error(
      "Item must be used inside of a Object, otherwise it will not function correctly."
    );
  }

  // Because of TypeScript's type narrowing, if we make it past
  // the error the compiler knows that context is always defined
  // at this point, so we don't need to do any conditional
  // checking on its values when we use this hook!
  return context;
};

const DialogProvider: React.FC<any> = ({ children }) => {
  const [dialogState, setDialogState] = useState<{
    type?: "warning" | "alert";
    header?: React.ReactElement | string;
    message?: React.ReactElement | string;
    onConfirm?: () => void;
    show: boolean;
  }>({
    show: false,
  });

  const openWarningDialog = (
    message: ReactElement | string,
    header: string,
    onConfirm?: () => void
  ) => {
    return setDialogState({
      type: "warning",
      header: header || "Warning",
      message: message,
      onConfirm,
      show: true,
    });
  };

  const openAlertDialog = (message: ReactElement | string, header?: string) => {
    return setDialogState({
      type: "alert",
      header: header || "Action not permitted",
      message: message,
      show: true,
    });
  };

  const closeDialog = () => {
    setDialogState({
      ...dialogState,
      show: false,
    });
  };

  return (
    <FlowDialogContext.Provider value={{ openAlertDialog, openWarningDialog }}>
      <FlowDialog
        onConfirm={dialogState.onConfirm}
        onClose={closeDialog}
        show={dialogState.show}
        message={dialogState.message}
        appearance={dialogState.type}
        header={dialogState.header}
      />
      {children}
    </FlowDialogContext.Provider>
  );
};

export default DialogProvider;
